import React from 'react';
import Cta from "../../components/Cta";
import {Image} from "../../components/Image";
import ArrowImage from "../../static/arrow.png";
import ArrowImageWhite from "../../static/arrow-white.png";
import ArrowImageYellow from "../../static/arrow-yellow.png";
import {GridContainer} from "../../components/Grid";
import "./back-home.scss";
import PropTypes from "prop-types";

function getImage(variant) {
  switch (variant) {
    case 'white':
      return ArrowImageWhite;
    case 'yellow':
      return ArrowImageYellow;
    default:
      return ArrowImage;
  }
}

function BackHome({variant = 'default'}) {
  return (
    <GridContainer className={`back-home back-home--${variant}`}>
      <Cta
        to="/"
        variant="link">
        <Image
          className="back-home__image"
          variant="cta"
          src={getImage(variant)}
          alt="left arrow"/> BACK HOME
      </Cta>
    </GridContainer>
  )
}

BackHome.propTypes = {
  variant: PropTypes.string,
};

export default BackHome
