import PropTypes from 'prop-types'
import React from 'react';
import {Grid, GridColumn, GridContainer} from '../../components/Grid'
import "./phone-numbers.scss";

const renderPhoneNumber = (item, index) => {
  return (
    <div className="phone-numbers__item" key={index}>
      {item}
    </div>
  );
};

function PhoneNumbers({children}) {
  const gridItems = children.map((x, i) => renderPhoneNumber(x, i));

  return (
    <GridContainer className="phone-numbers">
      <Grid variant="centered">
        <GridColumn columns="12">
          <Grid>
            <div className="phone-numbers__list">
              {gridItems}
            </div>
          </Grid>
        </GridColumn>
      </Grid>
    </GridContainer>
  )
}

PhoneNumbers.propTypes = {
  children: PropTypes.array.isRequired
};

export default PhoneNumbers
